.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 100%;
  z-index: 1000;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 72px;
  transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
  overflow-x: hidden;
}
.navbar.visible {
  transform: translateY(0);
}
.navbar.hidden {
  transform: translateY(-100%);
}
.mainContainer {
  max-width: 1440px;
  padding: 0 40px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  box-sizing: border-box;
}
.navbar.default {
  background-color: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.navbar.scrolled {
  background-color: var(--color-white-100);
}
.left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 24px;
}
.default .left {
  height: 72px;
  width: fit-content;
  background-color: var(--color-cream-100);
}
.logoLink {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.logo {
  height: 32px;
  width: auto;
  object-fit: contain;
  transition: filter 0.3s ease;
}
.default .logo {
  filter: brightness(0) invert(1);
}
.scrolled .logo {
  filter: brightness(0);
}
.menuContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 0 32px;
  gap: 24px;
  border-radius: 999px;
}
.scrolled .menuContainer {
  background-color: transparent;
}
.menuLink {
  font-family: var(--font-display);
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.02em;
  text-decoration: none;
  color: var(--color-white-100);
  transition: opacity 0.2s;
  white-space: nowrap;
}
.menuLink:hover {
  opacity: 0.8;
  font-weight: 400;
}
.scrolled .menuLink {
  color: var(--color-blue-100);
}
.scrolled .menuLink:hover {
  opacity: 0.8;
}
.right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 12px;
  padding-right: 24px;
}
.contactButton {
  --button-border-color: var(--color-white-100);
}
.scrolled .contactButton {
  --button-border-color: var(--color-blue-100);
}
.contactButton .textPart,
.scrolled .contactButton .textPart {
  color: inherit;
}
.default .contactButton {
  color: var(--color-white-100);
}
.scrolled .contactButton {
  color: var(--color-blue-100);
}
.bookButton {
  --button-primary-fill: var(--color-cream-100);
}
@media (max-width: 1024px) {
  .menuContainer {
    display: none;
  }
  .navbar {
    grid-template-columns: 1fr 1fr;
  }
  .right {
    justify-content: flex-end;
  }
}
